<template lang="pug">
    v-container(fluid)
        Snackbar(ref="snackbar")
        v-layout(wrap).grid-player
            UserPlayerContent(ref="player" :players="players" @add="add" @edit="edit" @del="del" @show="show").xs12.md7
            UserPlayerContentDialog(ref="dialog" @submit="submit")
            UserPreviewer(ref="previewer" title="模型预览" subtitle="(选中左侧角色预览)").xs12.md5
</template>

<script>
    import Snackbar from '@/components/common/Snackbar.vue'
    import UserPlayerContent from '@/components/user/player/UserPlayerContent.vue'
    import UserPlayerContentDialog from '@/components/user/player/UserPlayerContentDialog.vue'
    import UserPreviewer from '@/components/user/UserPreviewer.vue'
    export default {
        name: 'UserPlayer',
        components: {
            Snackbar, UserPlayerContent, UserPlayerContentDialog, UserPreviewer
        },
        data: () => ({
            players: [],
        }),
        mounted () {
            this.fetchPlayers();
        },
        methods: {
            async fetchPlayers () {
                this.$store.dispatch('getPlayers')
                    .then(res => this.players = res.data.data.players)
                    .catch(err => console.log(err));
            },
            add () {
                this.$refs.dialog.show({ action: 'ADD', title: '创建新角色' });
            },
            edit (player) {
                this.$refs.dialog.show({ action: 'EDIT', title: '编辑角色' }, player)
            },
            del (player) {
                this.$refs.dialog.show({ action: 'DEL', title: '删除将不可恢复，确认要删除吗？', readonly: true }, player)
            },
            show (player) {
                // TODO
            },
            dispatchFailed (err) {
                let msg = err.message;
                if (err.response && err.response.data.msg)
                    msg = err.response.data.msg;
                this.$refs.snackbar.show('error', '操作失败: ' + msg);
            },
            async submit ({ action, player }) {
                switch (action) {
                    case 'ADD':
                        this.$store.dispatch('addPlayer', player).then(res => {
                            this.$refs.snackbar.show('success', '创建新角色成功！');
                            this.players.push(res.data.data.player);
                        }).catch(err => this.dispatchFailed(err));
                        break;
                    case 'EDIT':
                        this.$store.dispatch('editPlayer', player).then(() => {
                            this.$refs.snackbar.show('success', '修改角色成功！');
                            let el = this.players.find(el => el.id === player.id);
                            el.name = player.name;
                            el.model = player.model;
                        }).catch(err => this.dispatchFailed(err));
                        break;
                    case 'DEL':
                        this.$store.dispatch('delPlayer', player).then(() => {
                            this.$refs.snackbar.show('success', '删除角色成功！');
                            let index = this.players.indexOf(player);
                            this.players.splice(index, 1);
                        }).catch(err => this.dispatchFailed(err));
                        break;
                    default:
                        break;
                }
            },
            // async showModel (item) {
            //     if (!item.skinUrl) {
            //         this.$axios.get(`/api/skin/${item.name}.png`, {
            //             responseType: 'arraybuffer'
            //         }).then(res => {
            //             item.skinUrl = 'data:image/png;base64,' + btoa(new Uint8Array(res.data).reduce((data, byte) => data + String.fromCharCode(byte), ''));
            //             this.$refs.snackbar.show('info', `角色 ${item.name} 皮肤获取成功!`);
            //             this.$refs.previewer.setSkin(item.skinUrl);
            //         })
            //     } else {
            //         this.$refs.previewer.setSkin(item.skinUrl);
            //     }
            // }
        }
    }
</script>

<style scoped lang="stylus">
    .grid-player
        > *
            &:first-child
                padding-right 16px
                @media (max-width 959px)
                    padding-right 0
                    margin-bottom 8px
            &:last-child
                padding-left 16px
                @media (max-width 959px)
                    padding-left 0
                    margin-top 8px
</style>
