<template lang="pug">
    v-dialog(v-model="dialog" persistent width="400px")
        v-card
            v-card-title.subheading {{ dialogTitle }}
            v-card-text
                v-form(ref="form" lazy-validation).layout.wrap
                    v-flex(xs12 sm6)
                        v-text-field(
                            v-model="editedPlayer.name"
                            :label="name"
                            :rules="nameRules"
                            prepend-icon="mdi-flag"
                            :disabled="readonly"
                            counter
                        )
                    v-flex(xs12 sm6)
                        v-select(
                            v-model="editedPlayer.model"
                            :items="models"
                            :label="model"
                            :rules="modelRules"
                            prepend-icon="mdi-tag"
                            :disabled="readonly"
                        )
            v-card-actions
                v-spacer
                v-btn(flat color="red darken-1" @click="dialog = false") 取消
                v-btn(flat color="green darken-1" @click="submit") 确认
</template>

<script>
    export default {
        name: 'UserPlayerContentDialog',
        data: () => ({
            editedPlayer: {
                id: 0,
                name: '',
                model: ''
            },
            defaultPlayer: {
                id: 0,
                name: '',
                model: 'Steve'
            },
            name: '* 昵称',
            nameRules: [
                v => !!v || '请输入昵称!',
                v => /^([a-zA-Z0-9_]{3,16})$/.test(v) || '无效的昵称格式.',
            ],
            model: '* 模型',
            modelRules: [
                v => !!v || '请选择模型!',
            ],
            models: ['steve', 'alex'],
            action: '',
            loading: false,
            readonly: false,
            dialog: false,
            dialogTitle: '',
        }),
        methods: {
            show ({ title, action, readonly }, player) {
                this.readonly = readonly;
                this.editedPlayer = Object.assign({}, player || this.defaultPlayer);
                this.dialogTitle = title;
                this.dialog = true;
                this.action = action;
            },
            submit (evt) {
                evt.preventDefault();
                if (!this.$refs.form.validate()) return;
                this.$emit('submit', {
                    action: this.action,
                    player: this.editedPlayer,
                });
                this.dialog = false;
                this.dialogTitle = '';
                this.action = '';
            }
        }
    }
</script>

