<template lang="pug">
    v-flex
        v-toolbar(dense flat color="white").elevation-1
            v-toolbar-title.subheading 我的角色
            v-spacer
            v-btn(icon @click="$emit('add')")
                v-icon mdi-account-plus
        v-data-table(
            :headers="headers"
            :items="players"
            item-key="id"
            rows-per-page-text="每页行数"
        ).elevation-1
            template(#items="props")
                tr(@click="$emit('show', props.item)" style="cursor: pointer;")
                    td {{ props.item.id }}
                    td {{ props.item.name }}
                    td {{ props.item.model }}
                    td.layout.px-0.justify-end.toolbar-actions
                        v-icon(@click.stop="$emit('edit', props.item)") mdi-pencil
                        v-icon(@click.stop="$emit('del', props.item)") mdi-delete
            template(#no-data)
                td(colspan="4").text-xs-center 没有任何角色数据
</template>

<script>
    export default {
        name: 'UserPlayerContent',
        props: {
            players: {
                type: Array,
                default: () => [],
            }
        },
        data: () => ({
            headers: [
                { text: 'ID', value: 'id' },
                { text: '名称', value: 'name' },
                { text: '模型', value: 'model' },
                { text: '操作', value: 'actions', align: 'right', sortable: false }
            ],
        })
    }
</script>

<style scoped lang="stylus">
    .toolbar-actions
        > *
            &:first-child
                margin-right 24px
            &:last-child
                margin-right 24px
                @media (max-width 959px)
                    margin-right 16px
</style>
